'use client'
import{useRouter} from'next/navigation';
import DashboardCards from '../../components/DashboardCards';
import VehicleTable from '../../components/VehicleTable';

export default function DashboardPage() {
  return (
    <div className="space-y-6">
      <h1 className="text-2xl font-bold text-white">TMS Dashboard</h1>
      
      <DashboardCards />
      
      <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
        <div className="lg:col-span-2">
          <div className="bg-gray-800/50 backdrop-blur-sm rounded-xl border border-gray-700 p-6">
            <h2 className="text-lg font-semibold mb-4 text-cyan-400">Recent Vehicle Activity</h2>
            <VehicleTable />
          </div>
        </div>
        
        <div className="space-y-6">
          <div className="bg-gray-800/50 backdrop-blur-sm rounded-xl border border-gray-700 p-6">
            <h2 className="text-lg font-semibold mb-4 text-cyan-400">Quick Actions</h2>
            <div className="space-y-3">
              <button className="w-full bg-cyan-600 hover:bg-cyan-700 text-white py-2 px-4 rounded-lg transition-all duration-200 flex items-center justify-between">
                <span>Register New Vehicle</span>
                <span>+</span>
              </button>
              <button className="w-full bg-purple-600 hover:bg-purple-700 text-white py-2 px-4 rounded-lg transition-all duration-200 flex items-center justify-between">
                <span>Create Service Request</span>
                <span>üîß</span>
              </button>
              <button className="w-full bg-emerald-600 hover:bg-emerald-700 text-white py-2 px-4 rounded-lg transition-all duration-200 flex items-center justify-between">
                <span>Generate Report</span>
                <span>üìä</span>
              </button>
            </div>
          </div>
          
          <div className="bg-gray-800/50 backdrop-blur-sm rounded-xl border border-gray-700 p-6">
            <h2 className="text-lg font-semibold mb-4 text-cyan-400">System Status</h2>
            <div className="space-y-4">
              <div>
                <div className="flex justify-between text-sm mb-1">
                  <span>Vehicle Capacity</span>
                  <span>85%</span>
                </div>
                <div className="h-2 bg-gray-700 rounded-full overflow-hidden">
                  <div className="h-full bg-cyan-500 rounded-full" style={{ width: '85%' }}></div>
                </div>
              </div>
              <div>
                <div className="flex justify-between text-sm mb-1">
                  <span>Service Requests</span>
                  <span>12 pending</span>
                </div>
                <div className="h-2 bg-gray-700 rounded-full overflow-hidden">
                  <div className="h-full bg-purple-500 rounded-full" style={{ width: '60%' }}></div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}